* {
  &, &:before, &:after {
    box-sizing: border-box;
  }
}

body {
  background-color: #000;
  font-family: 'Lato', sans-serif;
  font-size: 1rem;
  line-height: 120%;
  color: #fff;
}

/* Link */

a {
  color: #fff;
  text-decoration: none;
  &:hover, &:focus {
    color: #fff;
  }
}